.image-content,
.card-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 14px;
  width: 100%;
}
.image-content {
  position: relative;
  row-gap: 5px;
  padding: 25px 0;
}
.overlay {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #4070f4;
  border-radius: 25px 25px 0 25px;
}
.overlay::before,
.overlay::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -40px;
  height: 40px;
  width: 40px;
  background-color: #4070f4;
}
.overlay::after {
  border-radius: 0 25px 0 0;
  background-color: #fff;
}
.card-image {
  position: relative;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background: #fff;
  padding: 3px;
}
.card-image .card-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 50%;
  border: 4px solid #4070f4;
}
.name {
  font-size: 18px;
  font-weight: 500;
  color: #333;
}
.description {
  font-size: 14px;
  color: #707070;
  text-align: center;
}
.button {
  border: none;
  font-size: 16px;
  color: #fff;
  padding: 8px 16px;
  background-color: #4070f4;
  border-radius: 6px;
  margin: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.button:hover {
  background: #265df2;
}

/* Owl Carousel CSS */

.owl-nav .owl-prev,
.owl-nav .owl-next {
  background-color: white;
  position: absolute;
  top: 50%;
}
.owl-nav .owl-prev {
  border: 2px solid white !important;
  left: 0;
  font-size: 60px !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25) !important;
  padding-top: 2rem;
  color: white;
  background-color: rgb(253, 253, 253) !important;
  border-radius: 6px;
  overflow: hidden;
}
.owl-nav .owl-next {
  border: 2px solid white !important;
  right: 0;
  font-size: 60px !important;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25) !important;
  padding-top: 2rem;
  color: white;
  background-color: rgb(253, 253, 253) !important;
  border-radius: 6px;
  overflow: hidden;
}
.owl-nav .owl-prev span,
.owl-nav .owl-next span {
  padding-right: 8px;
  padding-left: 8px;
}

@media (max-width: 600px) {
  .owl-nav {
    display: none !important;
  }
}
